___CSS_LOADER_EXPORT___.push([module.id, `.cisl-text {
    transform-box: border-box;
    transform-origin: center;
    text-anchor: middle;
    text-align: center;
    dominant-baseline: central;
    white-space: nowrap;
    cursor: default;
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */
}

.cisl--style.cisl-tick-minor {
    height: 1%;
    width: 0.2%;
    fill: lightgray;
}
.cisl--style.cisl-tick-major {
    height: 2%;
    width: 0.4%;
    fill: lightgray;
}
.cisl--style.cisl-break-major {
    font-family: monospace, monospace;
}
.cisl--style.cisl-label {
    outline: none;
    padding: 2px;
    background-color: transparent;
    border: 1px solid black;
    border-radius: 5px;
    font-family: monospace, monospace;
}
.cisl--style.cisl-rails-border {
    stroke: darkgray;
}
.cisl--style.cisl-rails {
    stroke: lightgray;
}
.cisl--style.cisl-bar {
    stroke: black;
}
.cisl--style.cisl-handle {
    fill: blue;
    rx: 1%;
}
